import React, { useState, useEffect, useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import { toast } from 'react-toastify';
import Select from '../common/Select';
import Input from '../common/Input';
import Button from '../common/Button';
import RadioButton from '../common/RadioButton';
import { randomBytes } from 'crypto';
import { validateProperty, validateForm, scrollToRef } from '../common/Form';
import { generateRandomString } from '../../utils/string.util';
import FileUpload from '../common/FileUpload';
import DownloadList from '../common/DownloadList';
import {
  getListAlgorithm,
  encryptFile,
  decryptFile,
  getKeyValue,
} from '../../services/symmertric.service';
import { calculatorLength, AESFilesSchema } from '../../validations/aes.schema';
function AESFile() {
  const listMode = [
    { id: 'textMode', text: 'Text', checked: true },
    { id: 'hexMode', text: 'Hex', checked: false },
  ];
  const listKeySize = [
    { id: '128', name: '128' },
    { id: '192', name: '192' },
    { id: '256', name: '256' },
  ];

  const listModeCipher = [
    { id: 'ecb', name: 'ECB' },
    { id: 'ctr', name: 'CTR' },
    { id: 'cfb8', name: 'CFB8' },
    { id: 'cfb1', name: 'CFB1' },
    { id: 'ofb', name: 'OFB' },
    { id: 'cbc', name: 'CBC' },
  ];
  const downloadListRef = useRef(null);
  const [showIV, setShowIV] = useState(false);
  const [algorithms, setAlgotithms] = useState([]);
  const [errors, setErrors] = useState({});
  const [downloadLinks, setDownloadLinks] = useState([]);
  const [percentage, setPercentage] = useState(0);
  const [progress, setProgress] = useState('Upload');
  const [statusLoading, setStatusLoading] = useState({
    encrypt: false,
    decrypt: false,
  });
  const [sizeValues, setSizeValues] = useState({
    keyLength: 0,
    ivLength: 0,
  });
  const [values, setValues] = useState({
    files: [],
    key: '',
    iv: '',
    isHexKey: false,
    isHexIv: false,
    keySize: '',
    mode: '',
  });
  const styleIV = useSpring({ opacity: showIV ? 1 : 0 });

  useEffect(() => {
    async function fetchData() {
      try {
        const listAlgorithms = await getListAlgorithm();
        setAlgotithms(listAlgorithms.data);
      } catch (error) {
        toast.error('Can not get list algorithms');
      }
    }
    fetchData();
  }, []);

  useEffect(() => {
    const algo = algorithms.find(
      (cipher) => cipher.id === `aes-${values.keySize}-${values.mode}`,
    );
    if (algo) {
      setShowIV(algo.hasOwnProperty('ivLength'));

      const sizes = { ...sizeValues };
      sizes['keyLength'] = Number(algo.keyLength);
      sizes['ivLength'] = Number(algo.ivLength) || 0;
      setSizeValues(sizes);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [values.keySize, values.mode]);

  const schemaFiles = AESFilesSchema(calculatorLength(algorithms, values));

  const handleChange = ({ currentTarget: input }) => {
    const errorsData = { ...errors };
    const error = validateProperty(schemaFiles, input);
    if (error) errorsData[input.name] = error;
    else delete errorsData[input.name];
    setErrors(errorsData);
    const data = { ...values };
    if (input.name === 'file') data[input.name] = input.files[0];
    data[input.name] = input.value;
    setValues(data);
  };

  const handleEncrypt = async () => {
    values.algorithm = `aes-${values.keySize}-${values.mode}`;
    const errors = validateForm(schemaFiles, values);
    setErrors(errors || {});
    console.log(errors);
    if (errors) return;
    try {
      setStatusLoading({ ...statusLoading, encrypt: true });
      setProgress('Uploading...');
      const payload = { ...values };
      delete payload.keySize;
      delete payload.mode;
      const response = await encryptFile(payload, (event) => {
        setPercentage(Math.round((100 * event.loaded) / event.total));
      });
      setProgress('Done');
      const listDownloadFile = response.data.filter(
        (file) =>
          !downloadLinks.some((item) => file.fileName === item.fileName),
      );
      const listDownload = [...listDownloadFile, ...downloadLinks];
      setDownloadLinks(listDownload);
      scrollToRef(downloadListRef);
    } catch (error) {
      setProgress('Error');
      if (error.response) {
        const { message } = error.response.data;
        toast.error(message);
      }
    } finally {
      setTimeout(
        () => setStatusLoading({ ...statusLoading, encrypt: false }),
        500,
      );
    }
  };

  const handleDecrypt = async () => {
    values.algorithm = `aes-${values.keySize}-${values.mode}`;
    const errors = validateForm(schemaFiles, values);
    setErrors(errors || {});
    if (errors) return;
    try {
      setStatusLoading({ ...statusLoading, decrypt: true });
      setProgress('Uploading...');
      const payload = { ...values };
      delete payload.keySize;
      delete payload.mode;
      const response = await decryptFile(payload, (event) => {
        setPercentage(Math.round((100 * event.loaded) / event.total));
      });
      setProgress('Done');
      const listDownloadFile = response.data.filter(
        (file) =>
          !downloadLinks.some((item) => file.fileName === item.fileName),
      );
      const listDownload = [...listDownloadFile, ...downloadLinks];
      setDownloadLinks(listDownload);
      scrollToRef(downloadListRef);
    } catch (error) {
      setProgress('Error');
      if (error.response) {
        const { message } = error.response.data;
        toast.error(message);
      }
    } finally {
      setTimeout(
        () => setStatusLoading({ ...statusLoading, decrypt: false }),
        500,
      );
    }
  };

  const handleChecked = ({ currentTarget: input }) => {
    const { id, name } = input;
    const data = { ...values };
    data[name] = id === 'hexMode';
    setValues(data);
  };

  const handleGeneator = (name, property) => {
    const data = { ...values };
    const mode = `isHex${name.charAt(0).toUpperCase() + name.slice(1)}`;
    data[name] = values[mode]
      ? randomBytes(sizeValues[property]).toString('hex')
      : generateRandomString(sizeValues[property]);
    console.log(sizeValues[property]);
    const errorsData = { ...errors };
    const error = validateProperty(schemaFiles, { name, value: data[name] });
    console.log(error);
    if (error) errorsData[name] = error;
    else delete errorsData[name];
    setErrors(errorsData);
    setValues(data);
  };

  const handleGetKeyValue = async (files) => {
    if (files.length > 0) {
      try {
        const data = { ...values };
        const response = await getKeyValue(files[0]);
        data.key = response.data;
        setValues(data);
      } catch (error) {
        if (error.response) {
          const { message } = error.response.data;
          toast.error(message);
        }
      }
    }
  };

  const handleGetFiles = (files) => {
    const data = { ...values };
    const listFiles = [...files];
    data.files = listFiles;
    setValues(data);
    setPercentage(0);
    setProgress('Upload');
  };

  const handleRemove = (file) => () => {
    const newFiles = [...downloadLinks];
    newFiles.splice(newFiles.indexOf(file), 1);
    setDownloadLinks(newFiles);
  };

  const handleRemoveAll = () => {
    setDownloadLinks([]);
  };

  return (
    <div className='card'>
      <div className='card-header'>TOOL AES ENCRYPT</div>
      <div className='card-body'>
        <div className='container' style={{ paddingTop: '0' }}>
          <form>
            <FileUpload
              error={errors['files']}
              percentage={percentage}
              progress={progress}
              onGetFiles={handleGetFiles}
            />
            <Select
              label='Key Size'
              name='keySize'
              options={listKeySize}
              //defaultValue={values.keySize}
              error={errors['keySize']}
              onChange={handleChange}
            />
            <Select
              label='Mode'
              name='mode'
              options={listModeCipher}
              // defaultValue={values.mode}
              error={errors['mode']}
              onChange={handleChange}
            />
            <Input
              label='Key'
              name='key'
              value={values.key}
              error={errors['key']}
              isHasCopy={values.key !== ''}
              onGetFile={handleGetKeyValue}
              onChange={handleChange}
            />
            <RadioButton
              name='isHexKey'
              radios={listMode}
              onChange={handleChecked}
              isEnableGen={values.mode !== '' && values.keySize !== ''}
              onGenerate={() => handleGeneator('key', 'keyLength')}
            />
            {showIV && (
              <animated.div style={styleIV}>
                <Input
                  label='Initial Vector'
                  name='iv'
                  value={values.iv}
                  error={errors['iv']}
                  isHasCopy={values.iv !== ''}
                  onChange={handleChange}
                />
                <RadioButton
                  name='isHexIv'
                  radios={listMode}
                  onChange={handleChecked}
                  isEnableGen={values.mode !== '' && values.keySize !== ''}
                  onGenerate={() => handleGeneator('iv', 'ivLength')}
                />
              </animated.div>
            )}
            <div className='mb-3'>
              <Button
                text='Encrypt'
                size='btn-lg'
                style={{ marginRight: '10px' }}
                isLoading={statusLoading.encrypt}
                //disabled={validateForm(schemaFiles, values) !== null}
                onClick={handleEncrypt}
              />
              <Button
                text='Decrypt'
                size='btn-lg'
                isLoading={statusLoading.decrypt}
                //disabled={validateForm(schemaFiles, values) !== null}
                onClick={handleDecrypt}
              />
            </div>
            <span ref={downloadListRef}></span>
            {downloadLinks.length > 0 && (
              <DownloadList
                list={downloadLinks}
                onRemove={handleRemove}
                onRemoveAll={handleRemoveAll}
              />
            )}
          </form>
        </div>
      </div>
    </div>
  );
}

export default AESFile;
