import http from './base-http.service';
import { uploadFilesSymmetric } from './file.service';
import config from '../config/default.json';

const apiEndpoint = config.api + '/symmetric';

const getListAlgorithm = () => {
  return http.get(apiEndpoint + '/algorithms');
};

const getKeyValue = (file) => {
  return http.getKeyValue(file);
};

const encryptText = (payload) => {
  return http.post(apiEndpoint + '/encrypt/text', payload);
};

const decryptText = (payload) => {
  return http.post(apiEndpoint + '/decrypt/text', payload);
};

const encryptFile = (payload, onUploadProgress) => {
  return uploadFilesSymmetric(apiEndpoint + '/encrypt/files', payload, onUploadProgress);
};

const decryptFile = (payload, onUploadProgress) => {
  return uploadFilesSymmetric(apiEndpoint + '/decrypt/files', payload, onUploadProgress);
};

export {
  getKeyValue,
  getListAlgorithm,
  encryptText,
  decryptText,
  encryptFile,
  decryptFile,
};
