import { React, useState } from 'react';
import { useSpring, animated } from 'react-spring';
import AESText from './AESText';
import AESFile from './AESFile';

function AES() {
  const [tab, setTab] = useState(false);
  const styleText = useSpring({ opacity: tab ? 0 : 1 });
  const styleFile = useSpring({ opacity: tab ? 1 : 0 });
  const handleChangeTab = () => {
    setTab(!tab);
  };

  return (
    <>
      <div className='container' style={{paddingTop: '90px'}}>
        <div className='row justify-content-center'>
          <div className='col-md-6'>
            <div className='nav-tab'>
              <nav className='nav nav-pills nav-fill'>
                <button
                  onClick={handleChangeTab}
                  className={'nav-link' + (!tab ? ' active' : '')}
                >
                  Text
                </button>
                <button
                  onClick={handleChangeTab}
                  className={'nav-link' + (tab ? ' active' : '')}
                >
                  File
                </button>
              </nav>
            </div>
            {!tab ? (
              <animated.div style={styleText}>
                <AESText />{' '}
              </animated.div>
            ) : (
              <animated.div style={styleFile}>
                <AESFile />
              </animated.div>
            )}
          </div>
        </div>
      </div>
    </>
  );
}

export default AES;
