import Joi from 'joi';

const message = (name, isBuffer, length) => {
  if (length === 0) return '"algorithm" is required';
  const typeMessage = isBuffer ? 'hex characters' : 'bytes';
  return `"${name}" is not valid. Key must be {#limit} ${typeMessage}`;
};

const calculatorLength = (algorithms, values) => {
  const algoInfo = algorithms.find((cipher) => cipher.id === `aes-${values.keySize}-${values.mode}`);
  if (!algoInfo)
    return { keyLength: 0, ivLength: 0, isHexKey: false, isHexIv: false };
  const { key, isHexKey, iv, isHexIv } = values;
  const isHasIV = algoInfo.hasOwnProperty('ivLength');

  const keyLength =
    key && isHexKey
      ? Buffer.alloc(algoInfo.keyLength).toString('hex').length
      : algoInfo.keyLength;

  const ivLength =
    iv && isHasIV && isHexIv // case has iv and iv is hex string
      ? Buffer.alloc(algoInfo.ivLength).toString('hex').length
      : isHasIV // case has iv and iv is normal string
      ? algoInfo.ivLength
      : 0;
  return { keyLength, ivLength, isHexKey, isHexIv };
};

const BaseSchema = ({ keyLength, ivLength, isHexKey, isHexIv }) => {
  // Original Schema
  const schema = Joi.object({
    algorithm: Joi.string().required().messages({
      'string.base': `"algorithm" should be a type of 'text'`,
      'any.required': `"algorithm' is not allowed to be empty`,
    }),
    keySize: Joi.string().required().default('128'),
    mode: Joi.string().required().default('ecb'),
    isHexKey: Joi.boolean().required(),
    isHexIv: Joi.boolean(),
    key: Joi.string()
      .required()
      .min(keyLength)
      .max(keyLength)
      // .when('isHexKey', {
      //   is: true,
      //   then: Joi.string().hex(),
      // })

      .messages({
        'string.base': `"key" should be a type of 'text'`,
        'string.max': message('key', isHexKey, keyLength),
        'string.min': message('key', isHexKey, keyLength),
        'string.hex': `"key" is not a valid hexadecimal string`,
        'any.required': `"key" is not allowed to be empty`,
      }),
  });

  if (ivLength === 0)
    return schema.keys({
      iv: Joi.optional(),
    });
  return schema
    .keys({
      iv: Joi.string().min(ivLength).max(ivLength),
      // iv: Joi.when(isHexIv, {
      //   is: Joi.exist().valid(true),
      //   then: Joi.string().min(ivLength).max(ivLength).hex(),
      //   otherwise: Joi.string().min(ivLength).max(ivLength),
      // }),
    })
    .messages({
      'string.base': `"iv" should be a type of 'text'`,
      'string.max': message('iv', isHexIv),
      'string.min': message('iv', isHexIv),
      'string.hex': `"iv" is not a valid hexadecimal string`,
      'any.required': `"iv" is not allowed to be empty`,
    });
};

const AESFilesSchema = ({ keyLength, ivLength, isHexKey, isHexIv }) => {
  return BaseSchema({ keyLength, ivLength, isHexKey, isHexIv }).keys({
    files: Joi.array().min(1).required(),
  });
};

const AESTextSchema = ({ keyLength, ivLength, isHexKey, isHexIv }) => {
  return BaseSchema({ keyLength, ivLength, isHexKey, isHexIv }).keys({
    text: Joi.string().required().messages({
      'string.base': `"text" should be a type of 'text'`,
      'any.required': `"text" is not allowed to be empty`,
    }),
  });
};

export { calculatorLength, AESTextSchema, AESFilesSchema };
